import React from "react"

export default class IndexPage extends React.Component {
  state = { note_text: "# Example note\n" }
  updateText = event => {
    const target = event.target
    const value = target.value

    this.setState({
      note_text: value
        .replace(
          ":boilerplate:",
          "# Big title\nThis is a small subtitle\n## Section 1\nSubtitle for section 1"
        )
        .replace(":smile:", "ðŸ˜„")
        .replace(":star:", "âœ´")
        .replace(":check:", "âœ”")
        .replace(":cross:", "âœ–"),
    })
  }
  render() {
    return (
      <div className="p-5">
        <div className="px-10 py-5">
          <button className="font-fira rounded p-2 bg-blue-200 text-blue-800 border border-blue-900 hover:bg-blue-500 outline-none">
            Save
          </button>
        </div>
        <p className="font-fira text-blue-800 text-xl px-10 my-5">Rendered</p>

        <div className="p-10 bg-blue-100 rounded">
          {this.state.note_text
            .toString()
            .split("\n")
            .map(inputline => {
              let line = inputline

              /*
             `/^<latex> (.*$)/gim.test(line) ? (
                <img
                  src={`https://latex.codecogs.com/gif.latex?${encodeURIComponent(
                    line.replace("<latex> ", "")
                  )}`}
                />
              )
               */
              return /^# (.*$)/gim.test(line) ? (
                <>
                  <p className="text-blue-900 text-4xl font-bold font-slab">
                    {line.replace("# ", "")}
                  </p>
                </>
              ) : /^## (.*$)/gim.test(line) ? (
                <>
                  <br />
                  <p className="text-blue-900 text-2xl font-bold font-slab">
                    {line.replace("## ", "")}
                  </p>
                </>
              ) : /^### (.*$)/gim.test(line) ? (
                <>
                  <br />
                  <p className="text-blue-900 text-xl font-bold font-slab">
                    {line.replace("### ", "")}
                  </p>
                </>
              ) : /^- (.*$)/gim.test(line) ? (
                <li className="text-blue-900 font-lato">
                  {line.replace("- ", "")}
                </li>
              ) : /^\s*$/gim.test(line) ? (
                <br />
              ) : /^<latex> (.*$)/gim.test(line) ? (
                <img
                  src={`https://latex.codecogs.com/svg.latex?${encodeURIComponent(
                    line.replace("<latex> ", "")
                  )}`}
                  alt="LaTeX"
                />
              ) : (
                <p className="text-blue-900 font-lato">{line}</p>
              )
            })}
        </div>
        <p className="font-fira text-blue-800 text-xl px-10 my-5">Source</p>
        <textarea
          className="w-full font-fira p-10 outline-none bg-blue-100 border text-blue-900 border-blue-900 rounded"
          onChange={this.updateText}
          value={this.state.note_text}
        />
      </div>
    )
  }
}
